<template>
    <div>
        <div class="container">
            <div>
                <h1>{{ $t("title.timer") }}</h1>
                <p class="lead">
                    {{ $t("description.timer") }}
                </p>
            </div>
            <div class="m-0 text-right">
                <button type="button" class="btn btn-link btn-lg" @click="fullscreen">
                    <font-awesome-icon icon="expand"/>
                </button>
                <button type="button" class="btn btn-link btn-lg" data-toggle="collapse" data-target="#timerSetting"
                        aria-expanded="false" aria-controls="timerSetting">
                    <font-awesome-icon icon="sliders-h"/>
                </button>
            </div>
            <div id="timerSetting" class="collapse bg-light pb-3 pt-3 mb-2">
                <div class="container">
                    <div class="row">
                        <div class="col-12 mb-3">
                            <div class="btn-group btn-group-toggle">
                                <label class="btn btn-outline-secondary" :class="{active: timerType === 'timer'}">
                                    <input type="radio" name="timerType" autocomplete="off" value="timer"
                                           v-model="timerType">{{ $t('text.timerMode') }}
                                </label>
                                <label class="btn btn-outline-secondary" :class="{active: timerType === 'countDown'}">
                                    <input type="radio" name="timerType" autocomplete="off" value="countDown"
                                           v-model="timerType">{{ $t('text.countDownMode') }}
                                </label>
                                <label class="btn btn-outline-secondary" :class="{active: timerType === 'now'}">
                                    <input type="radio" name="timerType" autocomplete="off" value="now"
                                           v-model="timerType">{{ $t('text.nowTimeMode') }}
                                </label>
                            </div>
                        </div>
                        <div class="col-12 col-md-4 mt-1">
                            <label for="change-font-size">{{ $t('text.fontSize') }}</label>
                            <input type="range" class="custom-range" min="1" max="20" step="0.5"
                                   id="change-font-size"
                                   v-model="style.fontSize">
                        </div>
                        <div class="col-12 col-md-4 mt-1">
                            <label for="choose-bg-color">{{ $t('text.bgColor') }}</label>
                            <div class="input-group-sm row">
                                <div class="col-8 pr-0">
                                    <input type="input" class="form-control" id="choose-bg-color"
                                           v-model="style.bgColor">
                                </div>
                                <div class="col-2 pl-0">
                                    <input type="color" class="form-control" v-model="style.bgColor">
                                </div>
                            </div>
                        </div>
                        <div class="col-12 col-md-4 mt-1">
                            <label for="choose-font-color">{{ $t('text.fontColor') }}</label>
                            <div class="input-group-sm row">
                                <div class="col-8 pr-0">
                                    <input type="input" class="form-control" id="choose-font-color"
                                           v-model="style.fontColor">
                                </div>
                                <div class="col-2 pl-0">
                                    <input type="color" class="form-control" v-model="style.fontColor">
                                </div>
                            </div>
                        </div>
                        <div class="col-12 col-md-6 mt-1">
                            <label>{{ $t('text.charStyle') }}</label>
                            <div class="input-group">
                                <div class="btn-group btn-group-toggle"
                                     style="overflow-x: auto">
                                    <label class="btn btn-outline-info" :class="{active: style.charId === '0'}">
                                        <input type="radio" name="char" autocomplete="off" value="0"
                                               v-model="style.charId" checked>{{ $t('text.none') }}
                                    </label>
                                    <label class="btn btn-outline-info" :class="{active: style.charId === '{'}">
                                        <input type="radio" name="char" autocomplete="off" value="{"
                                               v-model="style.charId">&nbsp;{&nbsp;&nbsp;}&nbsp;
                                    </label>
                                    <label class="btn btn-outline-info" :class="{active: style.charId === '{{'}">
                                        <input type="radio" name="char" autocomplete="off" value="{{"
                                               v-model="style.charId">&nbsp;<span v-pre>{{&nbsp;&nbsp;}}</span>&nbsp;
                                    </label>
                                    <label class="btn btn-outline-info" :class="{active: style.charId === '['}">
                                        <input type="radio" name="char" autocomplete="off" value="["
                                               v-model="style.charId">&nbsp;[&nbsp;&nbsp;&nbsp;]&nbsp;
                                    </label>
                                    <label class="btn btn-outline-info" :class="{active: style.charId === '<'}">
                                        <input type="radio" name="char" autocomplete="off" value="<"
                                               v-model="style.charId">&nbsp;<&nbsp;&nbsp;>&nbsp;
                                    </label>
                                    <label class="btn btn-outline-info" :class="{active: style.charId === '/'}">
                                        <input type="radio" name="char" autocomplete="off" value="/"
                                               v-model="style.charId">&nbsp;/&nbsp;&nbsp;/&nbsp;
                                    </label>
                                    <label class="btn btn-outline-info" :class="{active: style.charId === '//'}">
                                        <input type="radio" name="char" autocomplete="off" value="//"
                                               v-model="style.charId">&nbsp;//&nbsp;&nbsp;//&nbsp;
                                    </label>
                                    <label class="btn btn-outline-info" :class="{active: style.charId === '\/'}">
                                        <input type="radio" name="char" autocomplete="off" value="\/"
                                               v-model="style.charId">&nbsp;\&nbsp;&nbsp;/&nbsp;
                                    </label>
                                </div>
                            </div>
                        </div>
                        <div class="col-12 col-md-6 mt-1">
                            <label>{{ $t('text.theme') }}</label>
                            <div class="input-group">
                                <div v-for="(item, key) in supportStyleList"
                                     style="overflow-x: auto">
                                    <button type="button" class="btn btn-outline-dark mr-1"
                                            :style="{color: item.color, backgroundColor: item.bgColor}"
                                            @click="setColor(key)">
                                        {{ getChar[0] }}&nbsp;{{ getChar[1] }}
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div class="col-12 col-md-5 mt-1">
                            <label for="title-input">{{ $t('text.title') }}</label>
                            <input type="input" class="form-control" id="title-input" v-model="style.title">
                        </div>
                        <div class="col-12 offset-1 col-md-5 mt-1">
                            <label for="subtitle-input">{{ $t('text.subTitle') }}</label>
                            <input type="input" class="form-control" id="subtitle-input" v-model="style.subTitle">
                        </div>
                        <div class="form-group col-12 col-md-4 mt-1" v-if="timerType === 'countDown'">
                            <label for="hourInput">{{ $t('text.hour') }}</label>
                            <select id="hourInput" class="form-control" v-model="countDown.hour" @change="renderInitTime">
                                <option v-for="i in (0, 24)" :value="i">{{ i }}</option>
                            </select>
                        </div>
                        <div class="form-group col-12 col-md-4 mt-1" v-if="timerType === 'countDown'">
                            <label for="minInput">{{ $t('text.min') }}</label>
                            <select id="minInput" class="form-control" v-model="countDown.min" @change="renderInitTime">
                                <option v-for="i in (0, 59)" :value="i">{{ i }}</option>

                            </select>
                        </div>
                        <div class="form-group col-12 col-md-4 mt-1" v-if="timerType === 'countDown'">
                            <label for="secInput">{{ $t('text.sec') }}</label>
                            <select id="secInput" class="form-control" v-model="countDown.sec" @change="renderInitTime">
                                <option v-for="i in (0, 59)" :value="i">{{ i }}</option>

                            </select>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div id="timerBody"
             class="display-1 d-flex justify-content-center align-items-center align-items-stretch flex-fill"
             :style="{backgroundColor: style.bgColor, color: style.fontColor}">
            <div class="align-self-center" :style="{fontSize: style.fontSize * 2.2 + 'vw'}"
                 style="font-weight: lighter">
                {{ getChar[0] }}&nbsp;
            </div>
            <div class="align-self-center flex-column">
                <div class="text-center" :style="{fontSize: style.fontSize * 0.3 + 'vw'}">
                    {{style.title}}
                </div>
                <div class="text-center" :style="{fontSize: style.fontSize + 'vw'}">
                    {{ getHour }}:{{ getMin }}:{{ getSec }}
                </div>
                <div class="text-center" :style="{fontSize: style.fontSize * 0.15 + 'vw'}"
                     style="opacity:0.5">
                    {{style.subTitle}}
                </div>
                <div class="text-center mt-3" :style="{fontSize: style.fontSize * 0.15 + 'vw'}">
                    <font-awesome-icon icon="play" v-if="this.isPause" class="btn-icon fa-1x" @click="play"/>
                    <font-awesome-icon icon="pause" v-if="!this.isPause" class="btn-icon fa-1x" @click="pause"/>
                    &nbsp;&nbsp;
                    <font-awesome-icon icon="flag" class="btn-icon fa-1x"/>
                    &nbsp;&nbsp;
                    <font-awesome-icon icon="undo" v-if="timerType !== 'now'" class="btn-icon fa-1x" @click="reset"/>
                </div>
            </div>
            <div class="align-self-center" :style="{fontSize: style.fontSize * 2.2 + 'vw'}"
                 style="font-weight: lighter">
                &nbsp;{{ getChar[1] }}
            </div>
        </div>
    </div>
</template>

<script>
    import screenfull from "screenfull";
    import leftPad from "left-pad";
    import * as workerTimers from "worker-timers";

    export default {
        metaInfo() {
            return {
                title: this.$i18n.t('title.timer'),
                meta: [],
            }
        },
        data() {
            return {
                timerType: "timer",
                supportStyleList: [
                    {color: "#000000", bgColor: "#FFFFFF"},
                    {color: "#FFFFFF", bgColor: "#000000"},
                    {color: "#2fff5a", bgColor: "#000000"},
                    {color: "#FFF700", bgColor: "#2980B9"},
                    {color: "#E19C23", bgColor: "#2368E1"},
                    {color: "#E12F23", bgColor: "#23D5E1"},
                    {color: "#8223E1", bgColor: "#82E123"},
                    {color: "#2EA064", bgColor: "#A02E6A"},
                ],
                style: {
                    fontSize: 10,
                    bgColor: "#FFFFFF",
                    fontColor: "#000000",
                    charId: "{",
                    title: "",
                    subTitle: ""
                },
                time: {
                    hour: 0,
                    min: 0,
                    sec: 0,
                    microsec: 0
                },
                countDown: {
                    hour: 0,
                    min: 0,
                    sec: 0
                },
                startTime: 0,
                countDownTime: 0,
                pauseStartTime: 0,
                pauseTotalTime: 0,
                refreshTime: 10,
                requestId: null,
                isPause: true
            }
        },
        methods: {
            fullscreen() {
                screenfull.request($("#timerBody")[0]);
            },
            setColor(index) {
                this.style.fontColor = this.supportStyleList[index].color;
                this.style.bgColor = this.supportStyleList[index].bgColor;
            },
            play() {
                let now = Date.now();

                if (this.startTime === 0) {
                    this.startTime = now;
                    if (this.timerType === 'countDown') {
                        this.countDownTime = this.countDown.hour * 3600000 + this.countDown.min * 60000 + this.countDown.sec * 1000;
                    }
                }

                if (this.pauseStartTime) {
                    this.pauseTotalTime += now - this.pauseStartTime;
                    this.pauseStartTime = 0;
                }

                this.render();
                this.requestId = workerTimers.setInterval(this.render, this.refreshTime);
                this.isPause = false;
            },
            pause() {
                this.pauseStartTime = Date.now();
                workerTimers.clearInterval(this.requestId);
                this.requestId = null;
                this.isPause = true;
            },
            reset() {
                this.pause();
                this.pauseStartTime = 0;
                this.pauseTotalTime = 0;
                this.startTime = 0;
                this.time.hour = 0;
                this.time.min = 0;
                this.time.sec = 0;
                this.time.microsec = 0;
                this.isPause = true;

                if (this.timerType === 'countDown') {
                    this.renderInitTime();
                }
            },
            renderInitTime() {
                this.time.hour = this.countDown.hour;
                this.time.min = this.countDown.min;
                this.time.sec = this.countDown.sec;
            },
            render() {
                let displayTime;
                let nowDate = (new Date());

                switch (this.timerType) {
                    case "timer":
                        displayTime = nowDate.getTime() - this.startTime - this.pauseTotalTime;
                        break;
                    case "countDown":
                        displayTime = this.countDownTime - (nowDate.getTime() - this.startTime) + this.pauseTotalTime;
                        break;
                    case "now":
                        this.time.hour = nowDate.getHours();
                        this.time.min = nowDate.getMinutes();
                        this.time.sec = nowDate.getSeconds();
                        this.time.microsec = nowDate.getTime() % 1000;
                        return;
                }

                if (displayTime <= 0) {
                    this.pause();
                }

                if (displayTime > 3600000) {
                    this.time.hour = Math.floor(displayTime / 3600000);
                    displayTime -= this.time.hour * 3600000;
                } else {
                    this.time.hour = 0;
                }

                if (displayTime > 60000) {
                    this.time.min = Math.floor(displayTime / 60000);
                    displayTime -= this.time.min * 60000;
                } else {
                    this.time.min = 0;
                }

                if (displayTime > 1000) {
                    this.time.sec = Math.floor(displayTime / 1000);
                    displayTime -= this.time.sec * 1000;
                } else {
                    this.time.sec = 0;
                }

                this.time.microsec = displayTime;
            }
        },
        computed: {
            getChar() {
                switch (this.style.charId) {
                    case "0":
                        return ["", ""];
                    case "{":
                        return ["{", "}"];
                    case "{{":
                        return ["{{", "}}"];
                    case "[":
                        return ["[", "]"];
                    case "<":
                        return ["<", ">"];
                    case "/":
                        return ["/", "/"];
                    case "//":
                        return ["//", "//"];
                    case "\\/":
                        return ["\\", "/"];
                }
            },
            getHour() {
                return leftPad(this.time.hour, 2, 0);
            },
            getMin() {
                return leftPad(this.time.min, 2, 0);
            },
            getSec() {
                return leftPad(this.time.sec, 2, 0);
            }
        },
        watch: {
            timerType(value) {
                this.reset();

                if (value === 'now') {
                    this.play();
                }

            }
        },
        components: {},
        mounted() {

        }
    }
</script>

